.chat-header {
  text-align: center;
}

.chat-app {
  font-size: 18px;
  width: 100%;
}

.chat-main {
  padding: 10px;
  height: 33vh;
  margin-bottom: 5px;
  overflow-y: scroll;
}

.chat-main::-webkit-scrollbar {
  width: 0.25rem;
}

.chat-main::-webkit-scrollbar-track {
  background: #1e1e24;
}

.chat-main::-webkit-scrollbar-thumb {
  background: gray;
}

.chat-form {
  /* height: 10vh; */
  position: relative;
  bottom: 0;
  background-color: rgb(24, 23, 23);
  max-width: 728px;
  /* display: flex; */
  font-size: 1.5rem;
}

.chat-input {
  line-height: 1.5;
  width: 89%;
  display: inline-block;
  font-size: 1.5rem;
  background: white;
  color: white;
  outline: none;
  border: none;
  padding: 0 10px;
  border-radius: 0.25rem;
}

.chat-button {
  border: none;
  color: white;
  padding: 10px 20px;
  padding: 0.3rem 0.75rem;
  text-align: center;
  text-decoration: none;
  display: inline;
  cursor: pointer;
  font-size: 1.25rem;
  border-radius: 0.25rem;
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
  margin-left: 10px;
}

.chat-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

ul,
li {
  text-align: left;
  list-style: none;
}

.chat-p {
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
}

.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: white;
  background: #0b93f6;
  align-self: flex-end;
}
.received p {
  background: #e5e5ea;
  color: black;
}

.chat-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}

#send {
  height: 18px;
}

body {
  background-color: #0d1821;
  color: white;
  font-family: nobel, sans-serif !important;
  font-weight: 400;
  font-style: normal;
  overflow-x: hidden !important;
}

h1 {
  padding-bottom: 30px;
}

#title {
  font-size: 70px;
  padding-left: 40px;
  padding-top: 20px;
  color: #6e8894;
}

.userInfo {
  text-align: right;
  padding-right: 40px;
  padding-top: 20px;
  font-size: 20px;
}

#username {
  display: inline;
}

#circle {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: #6e8894;
  display: inline-block;
  margin-left: 4px;
  position: relative;
  top: 4px;
}

.cal {
  margin-top: 20px;
  height: 800px;
  width: 95%;
  border: rgb(159, 159, 159) solid 1px;
  border-radius: 10px;
  background-color: transparent;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 7px 6px 12px -10px rgba(107, 107, 107, 0.84);
}

.sideBox {
  margin-top: 20px;
  height: 800px;
  width: 95%;
  border: rgb(159, 159, 159) solid 1px;
  border-radius: 10px;
  background-color: transparent;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 7px 6px 12px -10px rgba(107, 107, 107, 0.84);
}

.form {
  margin-top: 100px;
  padding-left: 70px;
  padding-right: 70px;
  padding-top: 30px;
  padding-bottom: 30px;
  border: rgb(159, 159, 159) solid 1px;
  border-radius: 10px;
  box-shadow: 7px 6px 12px -10px rgba(107, 107, 107, 0.84);
}

.center {
  margin-left: auto;
  margin-right: auto;
}

#login {
  margin-left: auto;
  margin-right: auto;
}

#loginBtn {
  background-color: #85baa1;
  padding: 10px;
  font-size: 24px;
  margin-top: 30px;
}

#createBtn {
  background-color: #85baa1;
  padding: 10px;
  font-size: 24px;
  margin-top: 30px;
}

#acctBtn {
  background-color: #85baa1;
  padding: 10px;
  font-size: 24px;
  margin-top: 30px;
}

input {
  background-color: white;
  border: none !important;
  border-bottom: rgb(159, 159, 159) solid 1px !important;
  color: black !important;
}

input:hover {
  border: none;
}

.textCenter {
  text-align: center;
}

#plus {
  font-weight: 800;
  font-size: 24px;
  color: black;
}

.add {
  background-color: #6e8894;
  border-radius: 10px;
  height: 35px;
  width: 40px;
}

.dropdown {
  display: inline;
  margin-left: 10px;
}

textarea {
