}

.sign-out:hover {
  color: darkred;
}

.rbc-today {
  background-color: #6e8894;
  color: black;
}

.rbc-btn-group button {
  color: white;
}

.rbc-active {
  color: black;
}

.alignRight {
  text-align: right;
}

.center {
  position: fixed;
  top: 40%;
